.display-nav {
	display: block !important;
}

#nav-icon1 {
	width: 33px;
	z-index: 2;
	position: absolute;
	top: 4vh;
	display: none;
	/* margin: 50px auto; */
	right: 10vw;
	transform: rotate(0deg);
	transition: 0.5s ease-in-out;
	cursor: pointer;
}

#nav-icon1 span {
	display: block;
	position: absolute;
	height: 3px;
	width: 100%;
	background: #282c34;
	border-radius: 9px;
	opacity: 1;
	left: 0;
	transform: rotate(0deg);
	transition: 0.25s ease-in-out;
}

#nav-icon1 span:nth-child(1) {
	top: 0px;
}

#nav-icon1 span:nth-child(2) {
	top: 8px;
}

#nav-icon1 span:nth-child(3) {
	top: 16px;
}

#nav-icon1.open span:nth-child(1) {
	top: 12px;
	transform: rotate(135deg);
}

#nav-icon1.open span:nth-child(2) {
	opacity: 0;
	left: -60px;
}

#nav-icon1.open span:nth-child(3) {
	top: 12px;
	transform: rotate(-135deg);
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
	.links {
		display: none !important;
	}

	#nav-icon1 {
		display: inline-block;
	}

	.landing-home {
		flex-direction: column-reverse;
		width: 100%;
		justify-content: center;
		align-items: center;
		margin: 10vh auto;
	}

	.right-home,
	.left-home {
		width: 90%;
	}

	.insurance-wrapper {
		flex-direction: column;
		width: 90%;
	}

	.insurance {
		margin: 0.5rem;
	}

	.nav {
		padding-bottom: 1vh;
	}

	.home-contents {
		height: 80vh;
	}

	.how-it-works {
		height: 100vh;
	}

	.md-radio input[type="radio"] {
		display: none;
	}

	.input-submit {
		margin: 1rem;
	}

	.input,
	.input-submit {
		width: 300px;
	}
}

@media only screen and (max-height: 750px) {
	.how-it-works {
		height: 120vh;
	}
}

